import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home'
import AddChicago from '../views/AddChicago'
import AddDallas from '../views/AddDallas'
import About from '../views/About'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Home,
    name : 'Home'
  },
  {
    path: '/chicago',
    component: AddChicago,
    name : 'AddChicago'
  },

  {
    path: '/dallas',
    component: AddDallas
  },
  {
    path: '/about',
    component: About
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
